<template>
    <div class="notes__item">
        <div class="notes__item-top" :class="{between: !view}">
            <h2 class="notes__item-top-title">{{ item.title }}</h2>
            <p class="notes__item-top-date">{{ item.date }}</p>
        </div>
        <p class="notes__item-text">{{ item.text }}</p>
        <div class="notes__item-buttons">
            <button class="notes__item-buttons-btn edit" @click="$emit('change', item)">
                <img src="@/assets/images/edit.svg" alt="">
                <span>РЕДАКТИРОВАТЬ</span>
            </button>
            <button class="notes__item-buttons-btn del" @click="$emit('delNote', item)">
                <img src="@/assets/images/del.svg" alt="">
                <span>Удалить</span>
            </button>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            item: {
                typeof: Object
            },
            view: {
                typeof: Boolean
            }
        }
    }
</script>

