<template>
  <div class="wrapper">
    <Navbar
      @setVal="searchValue = $event"
    />
    <Notes 
      :notes="filterNotes"
      @delNote="delNote"
      @change="change"
    />
    <Transition name="modal">
      <Modal 
        @close="isModalOpen = false" 
        v-if="isModalOpen"
        @addNote="addNote"
        :edit="edit"
        :editedNote="editedNote"
        @changeNote="changeNote"
      />
    </Transition>
    <button class="addNote" @click="isModalOpen = true, edit = false" v-if="!isModalOpen">
      <img src="@/assets/images/edit.svg" alt="">
    </button>
  </div>
</template>

<script>
import Modal from './components/Modal.vue';
import Navbar from './components/Navbar.vue';
import Notes from './components/Notes.vue';

  export default {
    components: {
    Navbar,
    Notes,
    Modal
},
    data() {
      return {
        isModalOpen: false,
        notes: [],
        edit: false,
        editedNote: null,
        searchValue: ''
      }
    },
    created() {
      this.getNotes()
    },
    computed: {
      filterNotes() {
        return this.searchValue ? this.notes.filter(note => note.title.toLowerCase().includes(this.searchValue.toLowerCase())) : this.notes
      }
    },
    methods: {
      addNote(note) {
        this.notes.push(note)
      },
      delNote(item) {
        this.notes = this.notes.filter(note => note.id != item.id)
      },
      change(item) {
        this.isModalOpen = this.edit = true
        this.editedNote = item
      },
      changeNote(newNote) {
        this.notes.forEach(note => {
          if(note.id == newNote.id) {
            note.title = newNote.title
            note.text = newNote.text
            note.date = newNote.date
          }
        })
      },
      getNotes() {
        let localNotes = localStorage.notes
        if(localNotes) {
          this.notes = JSON.parse(localNotes)
        }
      }
    },
    watch: {
      notes: {
        handler() {
          localStorage.notes = JSON.stringify(this.notes)
        },
        deep: true
      }
    }
}
</script>

